<template>
  <div class="game-history-widget">
    <div class="game-history-widget__wrap">
      <table v-if="gameHistory.length">
        <thead>
          <tr>
            <th>Player</th>
            <th>Score</th>
            <th>Game Id</th>
            <th>Date</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(game, index) in gameHistory" :key="index" :class="{'highlight': index === lastAddedIndex, 'game-history-widget__table-row-im': userTag == game.userTag}">
            <td>{{ game.userTag }}</td>
            <td>{{ game.score }}₿</td>
            <td>{{ game.gameId }}</td>
            <td>{{ formatDate(game.timestamp) }}</td>
          </tr>
        </tbody>
      </table>
      <p v-else>No game history available</p>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import store from '@/vuex';

export default {
  name: 'GameHistoryWidget',
  computed: {
    ...mapGetters(['gameHistory']),
    userTag() {
      return store.getters.userTag;
    }
  },
  data() {
    return {
      lastAddedIndex: null,
    };
  },
  watch: {
    gameHistory: {
      handler(newHistory) {
        if (newHistory.length) {
          this.lastAddedIndex = 0;
          setTimeout(() => {
            this.lastAddedIndex = null;
          }, 3000);
        }
      },
    },
  },
  methods: {
    ...mapActions(['fetchGameHistory']),
    formatDate(timestamp) {
      return new Date(timestamp).toLocaleString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    }
  },
  created() {
    this.fetchGameHistory();
  }
}
</script>

